<template>
  <div>
    <v-row>
      <v-col v-for="partner in partners" :key="partner.id" cols="12" sm="6" md="3">
        <v-card
          class="mx-auto"
        >
          <v-img
            :src="`${uploads}/${partner.img}`"
          ></v-img>

          <v-card-title>
            {{ partner.name }}
          </v-card-title>

          <v-card-subtitle>
            {{ partner.position }}
          </v-card-subtitle>

          <v-card-actions>
            <v-btn
              :color="themeColor2"
              text
            >
              Read more...
            </v-btn>

            <v-spacer></v-spacer>

            <v-btn
              icon
              @click="partner.show = !partner.show"
            >
              <v-icon>{{ partner.show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
            </v-btn>
          </v-card-actions>

          <v-expand-transition>
            <div v-show="partner.show">
              <v-divider></v-divider>

              <v-card-text>
                {{ partner.description }}
              </v-card-text>
            </div>
          </v-expand-transition>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'OurPartners',
  data: () => ({
      show: false,
      partners: [
        {
          id: '9905dfdb-7e27-4b0c-9647-52db27082144',
          name: 'Kinplus Technologies',
          position: 'ICT & Software Development',
          description: `I'm a thing. But, like most politicians, he promised more than he could deliver. You won't have time for sleeping, soldier, not with all the bed making you'll be doing. Then we'll go with that data file! Hey, you add a one and two zeros to that or we walk! You're going to do his laundry? I've got to find a way to escape.`,
          img: 'kinplus.png',
          show: false
        },
      ]
    }),
    computed: {
      ...mapState(['themeColor2', 'uploads'])
    },
}
</script>
